<script>
import "@/styles/sidebar.css";
import axios from "axios";
import { jwtDecode } from "jwt-decode";

export default {
  name: "SideBar",
  data() {
    return {
      routeGroups: {
  "/calendar": "appointments",
  "/appointments": "appointments",
  "/agenda-schedules": "appointments",

  "/reports/appointments-report": "reports",
  "/reports/contracts-by-agent-report": "reports",
  "/reports/location-efficiency-report": "reports",
  "/reports/operator-performance-report": "reports",

  "/contracts": "contracts",
  "/contracts/new": "contracts",

  "/commissioning/agents": "commissioning",
  "/commissioning/operators": "commissioning",

  "/user/profile": "settings",
  "/settings/email": "settings",
  "/settings/generate-api-key": "settings",
  "/settings/logs": "settings",
  "/settings/tables": "settings",
  "/users": "settings",
},
 

      subMenus: {
        appointments: false,
        settings: false,
        commissioning: false,
        contracts: false,
        reports: false,
      },

      user: {
        nome: "",
        cognome: "",
        role: "",
      },
      currentTime: "",
    currentDate: "",
    motivationalQuote: "",
    quotes: [
      "Sii il cambiamento che vuoi vedere nel mondo.",
      "Ogni giorno  una nuova opportunit.",
      "Dai il massimo, sempre.",
      "Il successo  una somma di piccoli sforzi.",
      "Non fermarti finch non sei fiero."
    ],
    };
  },

  watch: {
  /* isCollapsed(newVal) {
    if (newVal) {
      for (const key in this.subMenus) {
        this.subMenus[key] = false;
      }
    }
  }, */
  '$route.path'() {
  this.setActiveSubMenuFromRoute();
},

  isCollapsed(newVal) {
    if (newVal) {
      for (const key in this.subMenus) {
        this.subMenus[key] = false;
      }
    }
  },
  
},

  props: {
  isCollapsed: {
    type: Boolean,
    default: false,
  },
},


  methods: {
    setActiveSubMenuFromRoute() {
  const currentPath = this.$route.path;

  // Trova il gruppo associato alla rotta attuale
  const activeGroup = this.routeGroups[currentPath];

  if (activeGroup) {
    for (const key in this.subMenus) {
      this.subMenus[key] = key === activeGroup;
    }
  }
},


    toggleSubMenu(menu) {
      if (this.isCollapsed) return; // Se la sidebar  compressa, non fare nulla

      // Chiude tutti gli altri sottomenu
      for (const key in this.subMenus) {
        if (key !== menu) {
          this.subMenus[key] = false;
        }
      }

      // Alterna lo stato del sottomenu selezionato
      this.subMenus[menu] = !this.subMenus[menu];
    },

    logout() {
      const token = localStorage.getItem("token");

      if (!token) {
        console.warn("Nessun token trovato, esco comunque.");
        this.$router.push("/login");
        return;
      }

      axios
        .post(
          "https://app.gixflow.cloud/api/auth/logout",
          {},
          {
            headers: { Authorization: `Bearer ${token}` },
          }
        )
        .then(() => {
          console.log("Logout registrato nel backend");

          // Rimuove il token dal localStorage
          localStorage.removeItem("token");

          // Rimuove il token dagli header di Axios
          delete axios.defaults.headers.common["Authorization"];

          // Reindirizza alla pagina di login
          this.$router.push("/login");
        })
        .catch((error) => {
          console.error(
            "Errore nel logout:",
            error.response?.data || error.message
          );

          // Anche se la chiamata fallisce, puliamo comunque il token per sicurezza
          localStorage.removeItem("token");
          delete axios.defaults.headers.common["Authorization"];
          this.$router.push("/login");
        });
    },

    loadUserData() {
      const token = localStorage.getItem("token");
      if (token) {
        try {
          const decoded = jwtDecode(token);
          console.log("[DEBUG] Dati utente decodificati:", decoded);

          this.user.nome = decoded.nome || "Sconosciuto";
          this.user.cognome = decoded.cognome || "";
          this.user.role = decoded.role || "Nessun ruolo"; //  Ora il ruolo sar mostrato correttamente!
        } catch (error) {
          console.error("[DEBUG] Errore nella decodifica del token:", error);
        }
      }
    },
    updateDateTime() {
    const now = new Date();
    this.currentTime = now.toLocaleTimeString("it-IT", {
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
    });

    this.currentDate = now.toLocaleDateString("it-IT", {
      weekday: "long",
      day: "numeric",
      month: "numeric",
      year: "numeric",
    });
  },
  updateQuote() {
    const randomIndex = Math.floor(Math.random() * this.quotes.length);
    this.motivationalQuote = this.quotes[randomIndex];
  },

    /* getRoleName(roleId) {
      const roles = {
        1: "Admin",
        2: "Manager",
        3: "Operatore",
        4: "Commerciale",
        5: "Super Admin",
      };
      return roles[roleId] || "Nessun ruolo";
    }, */
  },

  mounted() {
    this.loadUserData();
  this.updateDateTime();
  this.updateQuote();
  this.setActiveSubMenuFromRoute();


  setInterval(this.updateDateTime, 1000); // aggiorna ora ogni secondo
  setInterval(this.updateQuote, 10000);   // cambia frase ogni 10 secondi
  },
};
</script>

